import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { find } from './funnelback';
import Query from './components/finder__query';
import Results from './components/finder__results';

const initialResponse = {
    bestBets: [],
    results: [],
    spell: null,
    summary: {
        currEnd: 0,
        currStart: 0,
        numRanks: 0,
        totalMatching: 0,
    },
};

function Finder(props) {
    const [funnelbackResponse, setResponse] = useState(initialResponse);
    const params = new URLSearchParams(window.location.search);

    useEffect(() => {
        const [request] = find(
            props.config.collection,
            params.get('query') || null,
            params.get('query')
                ? null
                : params.get('sort') || props.config.sort,
            params.get('sortdirection') || props.config.sortDirection,
            params.get('start_rank') || 1,
            params.get('num_ranks') || props.config.numRanks,
            null
        );
        request.then(data => setResponse(data));
    }, []);

    return (
        <form className="finder">
            <Query
                collection={props.config.collection}
                placeholder={props.config.placeholder}
                query={params.get('query') || ''}
            />
            <Results
                response={funnelbackResponse}
                type={props.config.resultCard}
                summariseAs={props.config.summariseAs}
            />
        </form>
    );
}

Finder.propTypes = {
    config: PropTypes.object,
};

export default Finder;
