import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { suggest } from '../funnelback';

function finder__query(props) {
    const [suggestions, setSuggestions] = useState([]);
    const [partialQuery, setPartialQuery] = useState(props.query);
    const [call, setCall] = useState({ cancel: () => {} });

    const input = (
        <input
            className="finder__query__input"
            name="query"
            placeholder={props.placeholder}
            type="text"
            value={partialQuery}
            onChange={e => {
                // keep  what they're typing
                setPartialQuery(e.target.value);

                /**
                 * if we have a request to the suggestions service in progress,
                 * cancel it. Stops old suggestions overwriting new ones because
                 * the requests can't keep up with fast typing.
                 */
                call.cancel();

                if (e.target.value) {
                    // input is populated, ask for suggestions
                    const [suggestionsPromise, newCall] = suggest(
                        props.collection,
                        e.target.value
                    );

                    // update our request cancel function for the new request
                    setCall({
                        cancel: () => {
                            newCall.cancel();
                        },
                    });

                    suggestionsPromise
                        .then(data => setSuggestions(data))
                        .catch(() => setSuggestions([]));
                } else {
                    // input is empty, empty suggestions
                    setSuggestions([]);
                }
            }}
        />
    );

    const suggestionsList = suggestions && suggestions.length > 0 && (
        <ul className="finder__query__suggestions">
            {suggestions &&
                suggestions.map(suggestion => (
                    <li key={suggestion}>
                        <button
                            type="button"
                            onClick={() => {
                                setPartialQuery(suggestion);
                                setSuggestions([]);
                            }}
                        >
                            {suggestion}
                        </button>
                    </li>
                ))}
        </ul>
    );

    return (
        <fieldset className="finder__query">
            <div>
                {input}
                <button type="submit" className="finder__query__submit">
                    <span>
                        <span className="fal fa-search" aria-hidden="true" />{' '}
                        Find
                    </span>
                </button>
            </div>
            {suggestionsList}
        </fieldset>
    );
}

finder__query.propTypes = {
    collection: PropTypes.string,
    placeholder: PropTypes.string,
    query: PropTypes.string,
};

export default finder__query;
